input {
  margin: 0;
  padding: 0;
  border: 0;
  font-weight: normal;
  vertical-align: baseline;
}
a {
  color: #7e8c8d;
  text-decoration: none;
  -webkit-backface-visibility: hidden;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
li {
  list-style: none;
}
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}
::-webkit-scrollbar-track-piece {
  background-color: rgba(0, 0, 0, 0.2);
  -webkit-border-radius: 6px;
}
::-webkit-scrollbar-thumb:vertical {
  height: 5px;
  background-color: rgba(125, 125, 125, 0.7);
  -webkit-border-radius: 6px;
}
::-webkit-scrollbar-thumb:horizontal {
  width: 5px;
  background-color: rgba(125, 125, 125, 0.7);
  -webkit-border-radius: 6px;
}
input:focus,
button:focus {
  outline: none;
}
.clearfix {
  display: inline-block;
}
.clearfix::after {
  content: ".";
  display: block;
  height: 0;
  line-height: 0;
  visibility: hidden;
  clear: both;
}
@font-face {
  font-family: 'icomoon';
  src: url('assets/fonts/icomoon.eot?k0jdef');
  src: url('assets/fonts/icomoon.eot?k0jdef#iefix') format('embedded-opentype'), url('assets/fonts/icomoon.ttf?k0jdef') format('truetype'), url('assets/fonts/icomoon.woff?k0jdef') format('woff'), url('assets/fonts/icomoon.svg?k0jdef#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-back:before {
  content: "\e900";
}
.icon-password:before {
  content: "\e901";
}
.icon-user:before {
  content: "\e902";
}
body {
  overflow: hidden;
}
.login-body {
  background: #2b2e2f url(assets/images/noise.png);
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-repeat: repeat;
  background-attachment: fixed;
}
.login-wrap {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 260px;
}
.login-row {
  position: relative;
  height: 35px;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  margin-bottom: 10px;
  font-size: 12px;
  color: rgba(255, 255, 255, 0.9);
}
.login-row i {
  position: absolute;
  left: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.login-row input {
  height: 100%;
  width: 100%;
  background: transparent;
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  text-indent: 30px;
  color: #FFFFFF;
}
.login-row ::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.9);
}
.login-btn {
  height: 44px;
  width: 100%;
  border-radius: 44px;
  background: #f73668;
  border: 0;
  color: #FFFFFF;
  cursor: pointer;
  margin: 30px auto;
  display: block;
  text-align: center;
  -webkit-transition: all 0.3s ease-in;
  transition: all 0.3s ease-in;
}
.login-btn:hover {
  -webkit-box-shadow: 0 5px 5px rgba(0, 0, 0, 0.3);
          box-shadow: 0 5px 5px rgba(0, 0, 0, 0.3);
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}
.login-btn:active {
  background: rgba(247, 54, 104, 0.8);
}
.login-anime {
  opacity: 0;
}
.back {
  position: absolute;
  top: 30px;
  left: 100px;
  color: #FFFFFF;
  cursor: pointer;
  font-size: 24px;
  opacity: 0;
}
.index-body {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.index-body .left-bar,
.index-body .right-bar {
  background-image: url(assets/images/noise.png);
  background-repeat: repeat;
  background-attachment: fixed;
  position: absolute;
  top: 0;
  bottom: 0;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}
.index-body .left-bar {
  left: 0;
  width: 100%;
  background-color: #FFFFFF;
}
.index-body .left-bar .meun-button {
  width: 40px;
  height: 40px;
  right: 30px;
  top: 20px;
  position: absolute;
  padding-top: 8px;
  overflow: hidden;
  cursor: pointer;
}
.index-body .left-bar .meun-button i {
  width: 20px;
  height: 2px;
  background: #2b2e2f;
  display: block;
  margin: 5px auto;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}
.index-body .right-bar {
  right: 0;
  width: 0%;
  background-color: #2b2e2f;
}
.index-body.active .left-bar {
  left: -75px;
  z-index: 1;
  -webkit-box-shadow: 20px 0 50px rgba(0, 0, 0, 0.7);
          box-shadow: 20px 0 50px rgba(0, 0, 0, 0.7);
}
.index-body.active .left-bar .meun-button i:nth-of-type(1) {
  -webkit-transform-origin: left center;
          transform-origin: left center;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.index-body.active .left-bar .meun-button i:nth-of-type(2) {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  opacity: 0;
}
.index-body.active .left-bar .meun-button i:nth-of-type(3) {
  -webkit-transform-origin: left center;
          transform-origin: left center;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.index-body.active .right-bar {
  width: 75px;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.left-enter {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.left-enter-active,
.left-leave-active {
  -webkit-transition: .5s;
  transition: .5s;
}
.left-leave-to {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.right-enter {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.right-enter-active,
.right-leave-active {
  -webkit-transition: .5s;
  transition: .5s;
}
.right-leave-to {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}



